import React, { useEffect, useMemo, useState } from "react";
import wrapper from "../commons/WebComponentWrapper";
import PubSub from 'pubsub-js';
import { debounce } from 'lodash';

const SrsWidget = function (props, ref) {
    const [widgetOverrides, setWidgetOverrides] = useState({contents: props.widgetcontents || [], context: props.widgetcontext || {}, functions: props.widgetfunctions || {}});
    const [ts, setTs] = useState(Date.now() + "");
    const [contentId, setContentId] = useState(Date.now() + "");
    const [isReady, setIsReady] = useState(false);

    useEffect(() => {
        const widgetContentRefresh = async (eventName, eventData) => { 
            let widgetId = eventData.detail?.widgetid || "";
            let contents = eventData.detail?.payload;
            if(widgetId == props.elementid) {
                setWidgetOverrides(prevValue => { return {contents, context: prevValue.context, functions: prevValue.functions}; });
                setTs(Date.now() + "");
            }
        };

        const widgetContextRefresh = async (eventName, eventData) => { 
            let widgetId = eventData.detail?.widgetid || "";
            let context = eventData.detail?.payload;
            if(widgetId == props.elementid) {
                setWidgetOverrides(prevValue => { return {contents: prevValue.contents, context, functions: prevValue.functions}; });
                setTs(Date.now() + "");
            }
        };

        updateWidgetFunctions(props.widgetFunctions);

        const debouncedWidgetContentRefresh = debounce(widgetContentRefresh, 50);
        const debounceWidgetContextRefresh = debounce(widgetContextRefresh, 50);
        let tokens = [];
        if(isReady) {
            window.__SRSINIT__ = window.__SRSINIT__ || {};
            window.__SRSINIT__.widgetTokens = window.__SRSINIT__.widgetTokens || {};
            window.__SRSINIT__.widgetTokens[props.elementid] = window.__SRSINIT__.widgetTokens[props.elementid] || [];
            window.__SRSINIT__.widgetTokens[props.elementid].map(token => PubSub.unsubscribe(token));
            window.__SRSINIT__.widgetTokens[props.elementid] = [];
            tokens = window.__SRSINIT__.widgetTokens[props.elementid];
            tokens.push(PubSub.subscribe("WIDGET_CONTENTS_REFRESH", (async (eventName, eventData) => { debouncedWidgetContentRefresh(eventName, eventData) })));
            tokens.push(PubSub.subscribe("WIDGET_CONTEXT_REFRESH", (async (eventName, eventData) => { debounceWidgetContextRefresh(eventName, eventData) })));
        }
        return () => {
            tokens.map(token => PubSub.unsubscribe(token));
        }
    }, [isReady]);

    const updateWidgetFunctions = (widgetFunctions) => {
        let functions = typeof widgetFunctions == "string"? JSON.parse(widgetFunctions): widgetFunctions;
        window.__SRS__ = window.__SRS__ || {};
        window.__SRS__.functions = window.__SRS__.functions || {};
        window.__SRS__.functions = {...window.__SRS__.functions, ...functions};
        setIsReady(true);
    }

    useEffect(() => {
        setWidgetOverrides(prevValue => { return {contents: props.widgetcontents, context: prevValue.context, functions: prevValue.functions}; });
        setTs(Date.now() + "");
        setContentId(Date.now() + "");
    }, [props.widgetcontents]);

    useEffect(() => {
        setWidgetOverrides(prevValue => { return {contents: prevValue.contents, context: props.widgetcontext, functions: prevValue.functions}; });
        setTs(Date.now() + "");
    }, [props.widgetcontext]);

    useEffect(() => {
        setWidgetOverrides(prevValue => { return {contents: prevValue.contents, context: prevValue.context, functions: props.widgetfunctions}; });
        updateWidgetFunctions(props.widgetfunctions);
        setTs(Date.now() + "");
    }, [props.widgetfunctions]);

    let result = useMemo(() => {
        return (<srs-contentrenderer ts={ts} styles={JSON.stringify({minHeight: "fit-content", maxHeight: "unset", margin: "5px", overflow: "auto"})} 
            ref={(el) => {
                if (!el) return;
                el.elementid = props.elementid + "_" + contentId;
                el.widgetid = props.elementid;
                el.overrides = JSON.stringify(widgetOverrides);
                // el.clan = widgetOverrides.contents;
                // el.context = widgetOverrides.context;
                el.theme = props.theme;
          }} name={props.elementid} />)
    }, [props.elementid, widgetOverrides, contentId, props.theme, ts, isReady]);
    
    return (<div ref={ref}>
            {isReady? result: (<div>Loading...</div>)}
        </div>);
}

const dispatchedEvents = [];
const props = [{
    "name": "widgetcontents",
    "defaultValue": [],
    "type": "object"
}, {
    "name": "widgetcontext",
    "defaultValue": {},
    "type": "object"
}, {
    "name": "widgetfunctions",
    "defaultValue": {},
    "type": "object"
}];

const styles = `

`;

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsWidget), props, "", ["srs-contentrenderer"]);
export { WrappedComponent, props, styles, dispatchedEvents, slots }